import React, { memo, useMemo } from 'react';
import { COLORS, IRestProps, mergeRestProps } from '../../common';
import { StyledSliderMarks, Mark, MarkText } from './styled';

type MarksProps = {
  // 以下为内部属性，PropsFromSlider
  handleClickOnMark?: (mark:number) => void,
  sliderLength?:number,
  disable?: boolean,
  marks?: Array<any>,

  // 以下为用户自定义属性
  markBgColor?: string,
  markTextColor?: string,
  lineColor?: string,
  lineColorDisabled?: string,
} & IRestProps;

export const SliderMarks = memo((props: MarksProps) => {
  const { marks = [], handleClickOnMark, sliderLength = 0, disable,
    markBgColor = COLORS.LIGHT.LEVEL3_BASE_COLOR,
    markTextColor = COLORS.BLACK,
    style, ...rest } = props;
  const markStyle = useMemo(() => {
    return {
      backgroundColor: markBgColor,
    }
  }, [markBgColor]);
  const markTextStyle = useMemo(() => {
    return {
      color: markTextColor,
    }
  }, [markTextColor]);

  return (
    <>
      {marks.map((item, index) => (
        <StyledSliderMarks
          key={index}
          {...mergeRestProps({
            ...rest,
            style:{
              ...style,
              left: `${item / sliderLength * 100}%`,
              cursor: disable ? 'not-allowed' : 'pointer',
            }
          }, '')}
          onClick={() => handleClickOnMark?.(item)}
        >
          <Mark role="mark" style={markStyle} />
          <MarkText role="mark-text" style={markTextStyle}>{item}</MarkText>
        </StyledSliderMarks>
      ))}
    </>
  )
});

SliderMarks.displayName = 'sliderMarks';